html {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
body {
  background-color: #E6E6E6;
  background-image: url(data:image/png;base64,);
  background-repeat: repeat;
  padding: 40px 0;
}
.button {
  background-color: transparent;
  border: 1px solid #E6E6E6;
  border-radius: 3px;
  color: #333333;
  display: block;
  font-size: 0.9rem;
  margin: 0 auto;
  padding: 15px 40px;
  cursor: pointer;
}
.button:hover {
    background-color: #333333;
    color: #E6E6E6;
}
.app {
